import React, {useState, useEffect} from "react";

export default function PublicLiveFeed(props) {

    const [ companyName, setCompanyName] = useState([])
    const [ companyId, setCompanyId] = useState([])

    useEffect(() => {
        setCompanyName(props.match.params.companyName)
        setCompanyId(props.match.params.id)
        //you will call the master table here and filter the data you need for the live feed in the backend
    }, [props.match.params.id])

    console.log("companyName",companyName)
    console.log("companyId",companyId)


    return (
        <div>
            <h1>Public live feed</h1>
        </div>
    )
}
