import React, { useContext, useState } from "react";

import { DashboardContext } from "../../../contexts/dashboard-context.js";

import TotalWaste from "./reports/total-waste-report";
import DailyWasteChart from "./reports/daily-waste-report";
import HourlyWasteChart from "./reports/hourly-waste-report";
import WeeklyWaste from "./reports/weekly-waste-report";
import WeeklyWasteTrends from "./reports/weekly-trend-report";
import MonthlyWasteChart from "./reports/monthly-waste-report";
import AiPredictionsReport from "./reports/ai-predictions-report";
import FoodWasteTrackingProgress from "./reports/food-waste-target";
import WastePerCover from "./reports/waste-per-cover-report";
import WastePerSales from "./reports/waste-per-sales-report";
import WasteOnAdayOfTheWeek from "./reports/waste-on-a-day-of-the-week-report";
import DailySalesVsWasteReport from "./reports/daily-sales-vs-waste-report";

import Switches from "../libs/use-switches";

import {
  Radio,
  TableRow,
  Paper,
  TablePagination,
  TableHead,
  Table,
  TableContainer,
  TableCell,
  TableBody,
} from "@material-ui/core";

import Controller from "./controller.js";

import {
  setItemInLocalStorage,
  calculateTodaysDate,
  splitCamelCaseStringAndMakeFirstCharacterUpperCase
} from "../../../utilities.js"

import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";

export default function Dashboard(props) {

  const {
    showHourlyChart,
    showDailyChart,
    showWeeklyChart,
    showMonthlyChart,
    setShowHourlyChart,
    setShowDailyChart,
    setShowWeeklyChart,
    setShowMonthlyChart,
    showTotalWasteChart,
    setShowTotalWasteChart,
    foodWasteTargetSavings,
    setFoodWasteTargetSavings,
    aiPredictions,
    setAiPredictions,
    wastePerCover,
    setWastePerCover,
    wastePerSales,
    setWastePerSales,
    showWeeklyTrendChart,
    setShowWeeklyTrendChart,
    checked,
    handleChangeForCheckedAndAccordion,

    showWasteOnAdayOfTheWeek,
    setShowWasteOnAdayOfTheWeek,

    showDailySalesVsWasteReport,
    setShowDailySalesVsWasteReport,
  } = Switches()

  const {
    handleSiteChange,
    selectedValue,
    siteName,
    companyName,
    sitesID,
    sites,
    startDate,
    showPounds,
    userType,
    userDetail,
    handleSiteIDchange,
    selectedSiteValueID,
    companyID,

    weeklyDailyLabels,
    weeklyDailyRange
  } = useContext(DashboardContext)
  const rightColumnDisplay = [
    {
      id: '0',
      type: 'companyInfor'
    },
    {
      id: '1',
      type: 'hourlyWaste'
    },
    {
      id:'2',
      type: "dailyWaste"
    },
    {
      id:'3',
      type: "weeklyWaste"
    },
    {
      id:'4',
      type: "weeklyWasteTrend"
    },
    {
      id:'5',
      type: "monthlyWaste"
    },
    {
      id:'6',
      type: "totalWaste"
    },
    {
      id: "7",
      type: "foodWasteTargetSavings"
    },
    {
      id: "8",
      type: "aiPredictions"
    },
    { id: "9",
      type: "wastePerCover"
    },
    {
      id: "10",
      type: "salesPerCover"
    },
    {
      id: "11",
      type: "wasteOnAdayOfTheWeek"
    },
    {
      id: "12",
      type: "dailySalesVsWaste"
    }
  ]

  const leftColumnDisplay = []

  const twoColumns = {
    rightColumn: {
      name: "right-hand-side",
      items: rightColumnDisplay,
    },
    leftColumn: {
      name: "left-hand-side",
      items: leftColumnDisplay,
    }
  };

  const initialStateForColumns = JSON.parse(localStorage.getItem('twoColumns')) || twoColumns
  const [columns, setColumns] = useState(initialStateForColumns);

  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(4);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  const onDragEnd = (result, columns, setColumns) => {
    if (!result.destination) return;
    const { source, destination } = result;

    if (source.droppableId !== destination.droppableId) {
      const sourceColumn = columns[source.droppableId];
      const destColumn = columns[destination.droppableId];
      const sourceItems = [...sourceColumn.items];
      const destItems = [...destColumn.items];
      const [removed] = sourceItems.splice(source.index, 1);
      destItems.splice(destination.index, 0, removed);
      setColumns({
        ...columns,
        [source.droppableId]: {
          ...sourceColumn,
          items: sourceItems
        },
        [destination.droppableId]: {
          ...destColumn,
          items: destItems
        }
      });
      setItemInLocalStorage("twoColumns", {
        ...columns,
        [source.droppableId]: {
          ...sourceColumn,
          items: sourceItems
        },
        [destination.droppableId]: {
          ...destColumn,
          items: destItems
        }
      });

    } else {
      const column = columns[source.droppableId];
      const copiedItems = [...column.items];
      const [removed] = copiedItems.splice(source.index, 1);
      copiedItems.splice(destination.index, 0, removed);
      setColumns({
        ...columns,
        [source.droppableId]: {
          ...column,
          items: copiedItems
        }
      });
      setItemInLocalStorage("twoColumns", {
        ...columns,
        [source.droppableId]: {
          ...column,
          items: copiedItems
        }
      });

    }
  };

    return (
      <DragDropContext
      onDragEnd={(result) => onDragEnd(result, columns, setColumns)}
      >

          <div className="dashboard">

          {Object.entries(columns).map(([columnId, column], index) => {

          return (


            <Droppable droppableId="dashboard" droppableId={columnId} key={index}>

            {(provided) => (
              <main className="dashboard__reportsWrapper" {...provided.droppableProps} ref={provided.innerRef}>
                {column.items.map(({id, type}, index) => {

                  return (
                    <div key={index}>

                    {type === "companyInfor" &&
                      <>
                      <div className="dashboard__overviewSites--positioning showOnMobile">
                        <div className="dashboard__date__user dashboard__userInformationWrapper cooperHewittBold">
                            <div className="dashboard__userDateNameAndUserType cooperHewittBold">
                                {calculateTodaysDate()}
                              <div className="dashboard__date__user__type cooperHewittBold">
                                {userDetail.typeOfUser}
                              </div>
                              <div className="dashboard__date__user__name cooperHewittBold">
                                Welcome back
                                {" "}
                                {userDetail.fullName}
                              </div>
                            </div>
                          <div className="dashboard__companyName">{splitCamelCaseStringAndMakeFirstCharacterUpperCase(userDetail.companyname)}</div>
                        </div>
                      </div>

                      <div className="dashboard__overviewSitesReportTable report-subtitle showOnMobile">
                        <div className="report-subtitle">
                          Sites
                        </div>

                        <Paper className="dashboard__paper">
                          <TableContainer className="dashboard__tableContainer">
                            <Table
                              stickyHeader
                              aria-label="sticky table"
                              className="dashboard__table"
                            >
                              <TableHead>
                                <TableRow>
                                  <TableCell className="dashboard__tableCell"></TableCell>
                                  <TableCell className="dashboard__tableCell">
                                    <div className="report-subtitle">
                                      TOTAL CSP WASTE
                                    </div>
                                  </TableCell>
                                  <TableCell className="dashboard__tableCell">
                                    <div className="report-subtitle">
                                    MONTHLY CSP TREND
                                    </div>
                                  </TableCell>
                                </TableRow>
                              </TableHead>

                              <TableBody>
                                {sites
                                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                  .map((site, index) => (
                                    <TableRow key={index}>
                                      <TableCell className="dashboard__tableCell--style">
                                        <div className="dashboard__radio">
                                          <Radio
                                            checked={selectedSiteValueID === site.siteName}
                                            onChange={handleSiteIDchange}
                                            value={site.siteName}
                                            color="default"
                                            inputProps={{ "aria-label": "D" }}
                                          />
                                          <div className="dashboard__siteName--color">
                                            {" "}
                                            {site.siteName.replace(/[^A-Za-z]+/g, '').toLowerCase()}
                                          </div>
                                        </div>
                                      </TableCell>
                                      <TableCell className="dashboard__tableCell--style">

                                        {showPounds ? (
                                          <>
                                            <span className="dashboard__tableCell__icon applyMarginRightAndLeft applyCooperHewittBold">
                                              
                                            </span>
                                            {parseInt((site.totalWaste * 2.775).toFixed(0))}
                                          </>
                                        ) : (
                                          <>
                                          {parseInt(site.totalWaste.toFixed(0))}
                                            <span className="dashboard__tableCell__icon applyMarginRightAndLeft applyCooperHewittBold">
                                              Kg
                                            </span>
                                          </>
                                        )}

                                      </TableCell>
                                      <TableCell className="dashboard__tableCell--style">
                                        {showPounds ? (
                                          <>
                                          {parseInt((site.trends * 2.775).toFixed(0))}
                                          </>
                                        ) :
                                        <>
                                        {parseInt(site.trends.toFixed(0))}
                                            <span className="dashboard__tableCell__icon applyMarginRightAndLeft applyCooperHewittBold">
                                              Kg
                                            </span>
                                            </>
                                        }

                                      </TableCell>
                                    </TableRow>
                                  ))}
                              </TableBody>
                            </Table>
                          </TableContainer>

                          <TablePagination
                            rowsPerPageOptions={[2]}
                            component="div"
                            count={sites.length}
                            rowsPerPage={rowsPerPage}
                            page={page}
                            onChangePage={handleChangePage}
                            onChangeRowsPerPage={handleChangeRowsPerPage}
                          />
                        </Paper>
                      </div>
                      </>
                    }


                    {type === "totalWaste" &&
                      <Draggable draggableId={id} index={index}>
                        {(provided) => (
                        <section className="dashboard__report marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                          <TotalWaste
                            startDate={startDate}
                            showPounds={showPounds}
                            showTotalWasteChart={showTotalWasteChart}
                            setShowTotalWasteChart={setShowTotalWasteChart}
                            hideReport={checked.totalSiteWasteSwitch}
                          />
                        </section>
                        )}
                      </Draggable>
                      }

                      {type === "hourlyWaste" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (
                          <section className="dashboard__report marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                            <HourlyWasteChart
                              siteName={siteName}
                              showPounds={showPounds}
                              showHourlyChart={showHourlyChart}
                              setShowHourlyChart={setShowHourlyChart}
                              handleChangeForCheckedAndAccordion={handleChangeForCheckedAndAccordion}
                              hideReport={checked.hourlyChartSwitch}
                            />
                        </section>
                          )}
                        </Draggable>
                      }

                      {type === "dailyWaste" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (
                            <section className="dashboard__report marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <DailyWasteChart
                                siteName={siteName}
                                showPounds={showPounds}
                                showDailyChart={showDailyChart}
                                setShowDailyChart={setShowDailyChart}
                                handleChangeForCheckedAndAccordion={handleChangeForCheckedAndAccordion}
                                hideReport={checked.dailyChartSwitch}
                              />
                            </section>
                          )}
                        </Draggable>
                      }

                      {type === "weeklyWaste" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (
                          <section className="dashboard__report" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                            <div className="dashboard__weeklyWasteReportWrapper">
                              <WeeklyWaste
                                showPounds={showPounds}
                                showWeeklyChart={showWeeklyChart}
                                setShowWeeklyChart={setShowWeeklyChart}
                                handleChangeForCheckedAndAccordion={handleChangeForCheckedAndAccordion}
                                hideReport={checked.weeklyChartSwitch}
                              />
                            </div>
                          </section>
                          )}
                        </Draggable>
                      }

                      {type === "weeklyWasteTrend" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (
                          <section className="dashboard__report" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                            <div className="weeklyWasteTrend__reportWrapper">
                              <WeeklyWasteTrends
                                siteName={siteName}
                                showPounds={showPounds}
                                showWeeklyTrendChart={showWeeklyTrendChart}
                                setShowWeeklyTrendChart={setShowWeeklyTrendChart}
                                handleChangeForCheckedAndAccordion={handleChangeForCheckedAndAccordion}
                                hideReport={checked.weeklyWasteTrendSwitch}
                              />
                            </div>
                          </section>
                          )}
                        </Draggable>
                      }

                      {type === "monthlyWaste" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (
                            <section className="dashboard__report" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <MonthlyWasteChart
                                siteName={siteName}
                                showPounds={showPounds}
                                showMonthlyChart={showMonthlyChart}
                                setShowMonthlyChart={setShowMonthlyChart}
                                handleChangeForCheckedAndAccordion={handleChangeForCheckedAndAccordion}
                                hideReport={checked.monthlyChartSwitch}
                              />
                          </section>
                          )}
                        </Draggable>
                      }

                      {type === "aiPredictions" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (

                            <div className="dashboard__aiPredictionsReport marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <AiPredictionsReport
                                aiPredictions={aiPredictions}
                                setAiPredictions={setAiPredictions}
                                showPounds={showPounds}
                                hideReport={checked.aiPredictionSwitch}
                               />
                            </div>

                          )}
                        </Draggable>
                      }

                      {type === "foodWasteTargetSavings" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (

                            <div className="dashboard__foodWasteTrackingProgress marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <FoodWasteTrackingProgress
                                foodWasteTargetSavings={foodWasteTargetSavings}
                                setFoodWasteTargetSavings={setFoodWasteTargetSavings}
                                hideReport={checked.foodWasteTargetSwitch}
                              />
                            </div>

                          )}
                        </Draggable>
                      }

                      {type === "wastePerCover" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (

                            <div className="dashboard__draggableReport marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <WastePerCover
                                wastePerCover={wastePerCover}
                                setWastePerCover={setWastePerCover}
                                showPounds={showPounds}
                                hideReport={checked.wastePerCoverSwitch}
                                siteName={siteName}
                              />
                            </div>

                          )}
                        </Draggable>
                      }

                      {type === "salesPerCover" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (

                            <div className="dashboard__draggableReport marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <WastePerSales
                                wastePerSales={wastePerSales}
                                setWastePerSales={setWastePerSales}
                                showPounds={showPounds}
                                hideReport={checked.wastePerSalesSwitch}
                                siteName={siteName}
                              />
                            </div>

                          )}
                        </Draggable>
                      }

                      {type === "wasteOnAdayOfTheWeek" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (
                            <section className="dashboard__report marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <WasteOnAdayOfTheWeek
                                siteName={siteName}
                                showPounds={showPounds}
                                showWasteOnAdayOfTheWeek={showWasteOnAdayOfTheWeek}
                                setShowWasteOnAdayOfTheWeek={setShowWasteOnAdayOfTheWeek}
                                hideReport={checked.wasteOnAdayOfTheWeekSwitch}
                              />
                            </section>
                          )}
                        </Draggable>
                      }

                      {type === "dailySalesVsWaste" &&
                        <Draggable draggableId={id} index={index}>
                          {(provided) => (
                            <section className="dashboard__report marginBottomBy1rem" {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
                              <DailySalesVsWasteReport
                                siteName={siteName}
                                showPounds={showPounds}
                                showDailySalesVsWasteReport={showDailySalesVsWasteReport}
                                setShowDailySalesVsWasteReport={setShowDailySalesVsWasteReport}
                                hideReport={checked.dailySalesVsWasteReportSwitch}
                                weeklyDailyLabels={weeklyDailyLabels}
                                weeklyDailyRange={weeklyDailyRange}
                              />
                            </section>
                          )}
                        </Draggable>
                      }

                    </div>
                  )
              })}
              {provided.placeholder}
              </main>
            )}



            </Droppable>

          )

          })}

          <Controller
            checked={checked}
            showHourlyChart={showHourlyChart}
            setShowHourlyChart={setShowHourlyChart}
            sitesID={sitesID}
            handleSiteChange={handleSiteChange}
            selectedValue={selectedValue}
            allsites={sites}
            showPounds={showPounds}
            companyName={companyName}
            userType={userType}
            handleSiteIDchange={handleSiteIDchange}
            selectedSiteValueID={selectedSiteValueID}
            companyID={companyID}
          />

        </div>

      </DragDropContext>
    );

}


